@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap);
*{
  margin: 0;
  padding: 0;
}

body{
  width: 100vw;
  background-color: #e2e2e2;
}

nav {
    width: 100%;
    height: 100px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;  
    -webkit-justify-content: space-between;  
            justify-content: space-between; 
    background-color: #FF8855;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    line-height: 100px;
}

h1{
    font-family: Montserrat;
    font-size: 18px;
    color: #fff;
    margin-left: 8%;
}

nav form{
    width: 30%;
    height: 20%;
    margin-top: 30px;
    margin-left: -40%;
    padding: 10px;
    border: 1px solid #fff;
    border-radius: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

nav form span{
    color: #fff;
    margin-top: -0.4%;
}

nav form input{
    width: 100%;
    border: none;
    outline: none;
    background: none;
    font-family: Poppins;
    font-size: 12px;
    font-weight: 200;
    color: #fff;
}

nav form input::-webkit-input-placeholder{
    color: #fff;
}

nav form input:-ms-input-placeholder{
    color: #fff;
}

nav form input::placeholder{
    color: #fff;
}

nav a{
    margin-right: 8%;
    font-family: Poppins;
    font-size: 16px;
    font-weight: 300;
    color: #fff;
    text-decoration: none;
}

nav a:hover{
    text-decoration: underline;
}
.dayContainer{
    width: 200px;
    height: 250px;
    padding-top: 2vh;
    padding-bottom: 2vh;
    border-radius: 25px;
    background-color: #fff;
    box-shadow: 0px 8px 24px 0px #00000015;
}

.dayIcon{
    height: 125px;
    width: 125px;
    margin-left: 37.5px;
}

.dayContainer p {
    font-family: Poppins;
    color: #000;    
    margin-left: 15%;
}

.dayTemprature{
    font-size: 48px;
    font-weight: 600;
    margin-top: 5%;
}

.dayDate{
    margin-top: -8%;
    font-size: 14px;
    font-weight: 400;
}



.weekContainer{
    height: 580px;
    margin-top: 2%;
    margin-left: 6%;
    width: 30%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-gap: 40px 40px;
    gap: 40px 40px;
}
.activeDayContainer{
    width: 600px;
    height: 580px;
    margin-left: 8%;    
    margin-top: 2%;
    padding-top: 2vh;
    padding-bottom: 2vh;
    margin-bottom: 4vh;
    border-radius: 25px;
    background-color: #fff;
    box-shadow: 0px 8px 24px 0px #00000015;
    overflow: hidden;
}

.placeholder{
    font-size: 24px;
    font-weight: 700;
}

.activeDayFlex{
    width: 75%;
    margin: auto;
    margin-top: 3%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.activeDayIcon{
    background-repeat: no-repeat;
    -webkit-transform: scale(1.25, 1.25);
            transform: scale(1.25, 1.25);
    width: 150px;
    height: 150px;
    margin-top: 10%;
    margin-right: 5%;
    margin-left: 3%; 
}

.activeDayPContainer{
    -webkit-flex-direction: column;
            flex-direction: column;
    padding-top: 3%;
}

.activeDayContainer p {
    font-family: Poppins;
    color: #000;    
    margin-left: 15%;
}

.activeDayPContainer .activeDayTemprature{
    font-size: 72px;
    font-weight: 600;
    margin-bottom: -15%;
}

.activeDayPContainer .activeDayDate{
    font-size: 24px;
    width: 100%;
    font-weight: 500!important;
}

.activeDayPContainer h5:nth-child(odd){
    margin-top: 1%;
}

.activeDayPContainer h5{
    width: 100%;
    font-family: Poppins;
    font-weight: 300;
    font-size: 12px;
    margin-left: 15%;
}

.iconDescContainer{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.iconContainer{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 250px;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-left: 15%;
    margin-top: 10%;
}

.iconContainer div{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction:row;
            flex-direction:row;
    line-height: 40px;
}

.iconContainer img{
    width:40px;
    height:40px;
}

.descriptionContainer{
    height:250px;
    line-height: 40px;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-top: 10.9%;
    margin-left: 3%;
}
button {
  cursor: pointer;
}

.appContainer {
  width: 1600px;
  margin: auto;
  margin-top: 3%;
  border-radius: 25px;
  box-shadow: 4px 8px 16px 0px #00000025;
}

.contentContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.contentBackground {
  height: 700px;
  background-color: #fff;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
}

.contentBackground h1{
  color: #000;
  font-family: Poppins;
  padding-top:3%;
  font-size: 32px;
}


